<template>
  <div id="app">
    <router-view />
  </div>
</template>
<script>
export default {
  name: "App",
  data() {
    return {};
  },
  methods: {
    //获取搞笑段子列视频表
    getJokeList() {
      const params = {
        count: 100,
        page: 1,
        type: "video",
      };
      this.$api
        .getJokeList(params)
        .then((response) => {
          const result = response.data;
          if (result.code == "200") {
            //  this.$Message.success('获取数据成功');
            result.result.forEach((element) => {
              this.baseData.video.push({
                type: element.type,
                title: element.text,
                poster: element.thumbnail,
                src: element.video,
              });
            });
            console.log(this.baseData.video);
          } else {
            this.$Message.error(result.message);
          }
        })
        .catch((error) => {
          this.$Message.error("请求错误");
        });
    },
    //获取搞笑段子列表
    getJokeByTextList() {
      const params = {
        return_data: 0,
        num: 100,
        app_key: "0D01C4CA26367BA195F8402DACC184C7",
        sign: "CDF64287311F947362FC7C69AF1F9C21",
      };
      this.$api
        .getJokeByTextList(params)
        .then((response) => {
          const result = response.data;
          if (result.ret == "200") {
            //  this.$Message.success('获取数据成功');
            let myData = [];
            let i = 3;
            result.data.joke[0].forEach((element, index) => {
              i += 1;
              myData.push({
                articleId: i,
                articleTitle: `笑话第 ${index + 1} 条`,
                src:
                  "http://www.yangqq.com/d/file/news/life/2018-04-27/762f99f369ae786f970477feeb3b9d77.jpg",
                isTop: false,
                subTitle: `笑话第 ${index + 1} 条`,
                details: `<p class='paragraph'> ${element}  </p>`,
              });
            });
            this.baseData.articleData = this.baseData.articleData.concat(
              myData
            );
            // console.log(this.baseData.articleData)
          } else {
            this.$Message.error(result.message);
          }
        })
        .catch((error) => {
          this.$Message.error("请求错误");
        });
    },
    //获取上传的图片列表
    getUploadRecordListList() {
      const params = {
        return_data: 0,
        order_type: 1,
        page: 1,
        perpage: 1000,
        app_key: "0D01C4CA26367BA195F8402DACC184C7",
        sign: "CDF64287311F947362FC7C69AF1F9C21",
      };
      this.$api
        .getUploadRecordListList(params)
        .then((response) => {
          const result = response.data;
          if (result.ret == "200") {
            //  this.$Message.success('获取数据成功');
            // console.log(result.data.list);
            result.data.list.forEach((element) => {
              this.baseData.photoOthers.push({
                modulePermission: true,
                title: element.file_name,
                desc: element.file_name,
                src: element.file_url,
              });
            });
          } else {
            this.$Message.error(result.message);
          }
        })
        .catch((error) => {
          this.$Message.error("请求错误");
        });
    },
  },
  mounted() {
    this.getJokeList();
    // setTimeout(() => {
    this.getJokeByTextList();
    this.getUploadRecordListList();
    // },0);
  },
};
</script>

<style lang="scss" src="@/assets/scss/app.scss">
</style>



